  constructor(props) {
    super(props);
    this.state = {
      assistantInput: "",
      assistantResponseOpen: false,
      assistantResponse: ""
    };
    this.contextRef = React.createRef();
  }
  randomSamplePhrase() {
    const phrases = [
      "Hey Gary!",
      "Show me what can you do!",
      "Go to work experience",
      "Go to education",
      "Show me publications",
      "Get me Gary",
      "What's Gary's phone number",
      "What's Gary's email address"
    ];
    const phrase = phrases[Math.floor(Math.random() * phrases.length)];
    return phrase;
  }
  fadeAssistantResponse() {
    setTimeout(() => {
      this.setState({
        assistantInput: this.state.assistantInput,
        assistantResponseOpen: false,
        assistantResponse: this.state.assistantResponse
      });
    }, 10000);
  }
  handleDialogFlowResponse(response) {
    console.log("Response:");
    console.log(response);
    console.log("Assistant: " + response.result.fulfillment.speech);

    this.setState({
      assistantInput: this.state.assistantInput,
      assistantResponseOpen: true,
      assistantResponse: response.result.fulfillment.speech
    });
    this.fadeAssistantResponse();
  }
  handleDialogFlowError(error) {
    console.log("Error:");
    console.log(error);
  }
  async overToDialogFlow(message) {
    const promise = client.textRequest(message);
    promise
      .then(this.handleDialogFlowResponse.bind(this))
      .catch(this.handleDialogFlowError.bind(this));
  }
