import React, { Component } from "react";
import { Input } from "semantic-ui-react";
import Assistant from "../assistant/assistant";
export default class Header extends Component {
  constructor(props) {
    super(props);
    this.state = {
      assistantInput: ""
    };
  }
  assistantHandler(evt) {
    console.log("Sending to dialogflow: " + this.assistantInput);
    this.setState({
      assistantInput: ""
    });
  }
  onChangeInput(evt) {
    if (evt.key === "Enter") {
      this.assistantHandler(evt);
    } else {
      this.assistantInput = evt.target.value;
    }
  }

  render() {
    let resumeData = this.props.resumeData;
    // alert(resumeData.memberOf.length);
    let socialLinks = resumeData.memberOf;
    return (
      <React.Fragment>
        <header id="home">
          <nav id="nav-wrap">
            <a className="mobile-btn" href="#nav-wrap" title="Show navigation">
              Show navigation
            </a>
            <a className="mobile-btn" href="#" title="Hide navigation">
              Hide navigation
            </a>
            <ul id="nav" className="nav">
              <li className="current">
                <a className="smoothscroll" href="#home">
                  Home
                </a>
              </li>
              <li>
                <a className="smoothscroll" href="#about">
                  About
                </a>
              </li>
              <li>
                <a className="smoothscroll" href="#resume">
                  Resume
                </a>
              </li>
              <li>
                <a className="smoothscroll" href="#portfolio">
                  Works
                </a>
              </li>
              <li>
                <a className="smoothscroll" href="#publication">
                  Publications
                </a>
              </li>
              <li>
                <a className="smoothscroll" href="#contact">
                  Contact
                </a>
              </li>
            </ul>
          </nav>

          <div className="row banner">
            <div className="banner-text">
              <ul className="social">
                {socialLinks &&
                  socialLinks.map(item => {
                    return (
                      <li key={item.name}>
                        <a href={item.url} target="_blank">
                          <i className={item.logo} />
                        </a>
                      </li>
                    );
                  })}
              </ul>
              <h1 className="responsive-headline">"I am {resumeData.name}."</h1>
              {/* <h3>
                {resumeData.role}, {resumeData.roleDescription}
              </h3> */}
              <h2>
                <Assistant />
              </h2>
              <hr />
            </div>
          </div>

          <p className="scrolldown">
            <a className="smoothscroll" href="#about">
              <i className="icon-down-circle" />
            </a>
          </p>
        </header>
      </React.Fragment>
    );
  }
}
